.current-weather-wrapper{
    color: white;
    padding: 25px 25px;
    border-bottom: 1px solid #efefef;
}

.overcast{
    background-image: url('../../library/images/overcast.jpg');
}

.overcast{
    background-color: gray!important;
}

.rain{
    background-image: url('../../library/images/rain.jpg');
}

.partly-cloudy-day{
    background-image: url('../../library/images/partly-cloudy.jpg');
}

.snow{
    background-image: url('../../library/images/snow.jpg');
}

.sun{
    background-image: url('../../library/images/sun.jpg');
}

.current-weather-wrapper p {
    opacity: .5;
    font-style: italic;
    text-align: center;
}

.current-weather-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    padding: 40px 0;
}

.current-weather-container .temperature{
    font-size: 30px;
    text-align: center;
}

.current-weather-container .location{
    text-align: center;
    font-size: 28px;
    font-style: italic;
}

.current-weather-container .weather-symbol{
    text-align: center;
    width: 100%;
    font-size: 28px;
    font-style: italic;
}

.change-city{
    padding: 15px 0;
    display: flex;
}

.change-city-btn{
    width: 100px;
    height: auto;
    border: none;
    outline: none;
    background-color: rgb(21, 121, 54);
    color: white;
    border-radius: 3px;
    padding: 5px;
    margin: 0 5px;
    transition: .2s ease;
}

.change-city-btn:hover{
    background-color: rgb(29, 160, 73);
}

.change-city-btn:active{
    background-color: rgb(35, 190, 87);
}

.city-select{
    display: block;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    width: 100%;
    max-width: 100%; 
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
    linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
  background-repeat: no-repeat, repeat;
  background-position: right .7em top 50%, 0 0;
  background-size: .65em auto, 100%;
}