@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap);
.current-weather-wrapper{
    color: white;
    padding: 25px 25px;
    border-bottom: 1px solid #efefef;
}

.overcast{
    background-image: url(/weatherapp/static/media/overcast.4cccec2e.jpg);
}

.overcast{
    background-color: gray!important;
}

.rain{
    background-image: url(/weatherapp/static/media/rain.653f2901.jpg);
}

.partly-cloudy-day{
    background-image: url(/weatherapp/static/media/partly-cloudy.2cdb2167.jpg);
}

.snow{
    background-image: url(/weatherapp/static/media/snow.2207f282.jpg);
}

.sun{
    background-image: url(/weatherapp/static/media/sun.9ddbb295.jpg);
}

.current-weather-wrapper p {
    opacity: .5;
    font-style: italic;
    text-align: center;
}

.current-weather-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    padding: 40px 0;
}

.current-weather-container .temperature{
    font-size: 30px;
    text-align: center;
}

.current-weather-container .location{
    text-align: center;
    font-size: 28px;
    font-style: italic;
}

.current-weather-container .weather-symbol{
    text-align: center;
    width: 100%;
    font-size: 28px;
    font-style: italic;
}

.change-city{
    padding: 15px 0;
    display: flex;
}

.change-city-btn{
    width: 100px;
    height: auto;
    border: none;
    outline: none;
    background-color: rgb(21, 121, 54);
    color: white;
    border-radius: 3px;
    padding: 5px;
    margin: 0 5px;
    -webkit-transition: .2s ease;
    transition: .2s ease;
}

.change-city-btn:hover{
    background-color: rgb(29, 160, 73);
}

.change-city-btn:active{
    background-color: rgb(35, 190, 87);
}

