/* WEATHER APP COLOR VARIABLES */

:root {
  --bg-color: rgb(243, 243, 243);
  --light-brown: rgba(238, 231, 223, 1);
  --dark-brown: rgb(61, 18, 1);
  --light-blue: rgba(171, 222, 215, .2);
  --dark-blue: rgba(77, 133, 129, 1);
}


/* ELEMENT STYLING */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: rgb(243, 243, 243);
  background-color: var(--bg-color);
}

h1, h2, h3, nav li {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
}

p {
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
}

nav {
  text-align: center;
  width: 100vw;
  position: fixed;
  background-color: rgb(247, 247, 247);
  box-shadow: 0 3px 6px rgba(0,0,0,0.06), 0 3px 6px rgba(0,0,0,0.13);
  padding: 0px 50px;
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  padding: 20px 30px;
}

nav ul li a {
  text-decoration: none;
  color: rgb(109, 109, 109);
  -webkit-transition: .15s ease;
  transition: .15s ease;
}

nav ul li a:hover {
  color: rgb(14, 14, 14);
}


